import React, { useState, useEffect, useCallback } from 'react';

import { withStyles } from '@material-ui/styles'
import Grid from '@material-ui/core/Grid'
import TextField from '@material-ui/core/TextField'


import styles from './taskform.styles'



const PresentationTaskForm = ({...props})=>{
    const {oldtask, classes } = props


    const [title, setTitle] = useState('')



    // const handleSubmit = e => {
    //     e.preventDefault();
    //     if (!title) return;
    //     props.handleAddTask(title)
    //     setTitle('') // reset state component
    // };

    const handleSubmit = useCallback(
        e=>{
            e.preventDefault();
            if (!title) return;
            if(oldtask.id!==''){
                // update task
                props.handleEditTitleTask({id: oldtask.id, title: title})
                props.handleResetFormState()
                // console.log('oldtask')
            }else {
                // add new task
                console.log('newtask')
                props.handleAddTask(title)

            }
            setTitle('') // reset state component

        },
        [props, oldtask, title]
    )

    // old: ok
    // const handleInputValue = e=>{
    //     setTitle(e.target.value)
    //     // console.log(title)
    // }
    // new
    const handleInputValue = useCallback(
        // updates "value" with event.target.value
        ({ target: { value } }) => setTitle(value),
        []
    );



    useEffect(
        () => {
            console.log('render form!');

            // setTitle(oldtask.title)
            // console.log(oldtask.title)
            // console.log(title)
            if(oldtask && oldtask.title!==''){
                console.log('oldtask.title')
                setTitle(oldtask.title)
                // console.log(oldtask.title)
                // console.log(title)

            } else {
                setTitle('') // reset input vaalue if dont editing title
            }
        },
        [oldtask]
    );

    // componentDidMount
    // useEffect(() => console.log('mounted'), []);

    // componentDidUpdate
    // useEffect(() => console.log('mounted or updated'));

    // componentWillMount and componentWillUnmount
    // useEffect(
    //     () => {
    //         effect
    //         return () => {
    //             cleanup
    //         };
    //     },
    //     [] // Only Run on Mount and Unmount
    // )

    // componentWillUnmount
    // the returned function will be called
    // before the component is removed from the UI.
    // useEffect(() => {
    //     return () => {
    //       console.log('will unmount');
    //     }
    // }, []);


    return (
        <Grid container justify="center" spacing={3}>

            <Grid item xs={6}>

                <form
                    className={classes.addform}
                    noValidate
                    autoComplete="off"
                    onSubmit={handleSubmit}
                >
                    <TextField
                        margin="normal"
                        variant="outlined"
                        fullWidth
                        placeholder="Placeholder"
                        helperText="Type new task name and hit enter key. ðŸ§¡. For a nice day."

                        id="add-new-task"
                        label="Title"
                        className={classes.textField}
                        value={title}
                        onChange={handleInputValue}
                    />
                </form>
                <h1>{oldtask.title}</h1>
                <p>{oldtask.id}</p>
            </Grid>

        </Grid>
    )
}




export default withStyles(styles)(PresentationTaskForm)
