import React from 'react';

import { withStyles } from '@material-ui/styles'

import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import IconButton from '@material-ui/core/IconButton'
import DeleteIcon from '@material-ui/icons/Delete'
import DoneIcon from '@material-ui/icons/Done'
import EditIcon from '@material-ui/icons/Edit'


import styles from './taskitem.styles'



/**
 *
 * @param {*} task from parent compoent
 * @param {*} handleDeleteTask from parent compoent
 * @param {*} handleEditStatusTask from parent compoent
 * @param {*} classes from mui
 *
 *
 */

const TaskItem = ({...props})=>{

    const {
        classes,
        task,
        handleDeleteTask,
        handleEditStatusTask,
        handleGetOldTask,
        handleResetForm,
    } = props

    const onEditState = () => {
        handleResetForm()
        handleEditStatusTask(task.id)
    }
    const onDeleteState = () => {
        handleResetForm()
        handleDeleteTask(task.id)
    }

    return (
        <ListItem button className={classes.taskitem}>
            <ListItemText
                id={task.id}
                primary={task.title}
                secondary={task.start}
            />
            <ListItemSecondaryAction>
                <IconButton
                    edge="end"
                    aria-label="done"
                    // onClick={()=>handleEditStatusTask(task.id)}
                    onClick = {onEditState}
                >
                    <DoneIcon color={task.status ? "action" : "secondary"} />

                </IconButton>

                <IconButton
                    edge="end"
                    aria-label="edit-title"
                    onClick={()=>handleGetOldTask({id: task.id, title: task.title})}
                >
                    <EditIcon color="secondary" />
                </IconButton>

                <IconButton
                    edge="end"
                    aria-label="delete"
                    // onClick={()=>handleDeleteTask(task.id)}
                    onClick={onDeleteState}
                >
                    <DeleteIcon color="secondary" />
                </IconButton>

            </ListItemSecondaryAction>
        </ListItem>
    )
}


export default withStyles(styles)(TaskItem)
