
import * as types from '../configs/action.types'

const initState = {
    id: '',
    title: '',
}

/**
 * handle old task and send to store
 * @param {*} state
 * @param {*} action
 */
const myReducer = (state=initState, action)=>{

    switch(action.type){

        case types.GET_OLD_TASK:
            console.log('action')
            return action.oldTask

        case types.RESET_FORM_STATE:
            return {id:'', title: ''}

        default:
            return state
    }
}

export default myReducer
