import * as types from '../configs/action.types'


export const listAll = ()=>({
    type: types.LIST_ALL
})
export const addTask = newtask=>({
    type: types.ADD_NEW_TASK,
    newtask
})
export const deleteTask = deletedTaskId=>({
    type: types.DELETE_TASK,
    deletedTaskId
})
export const editStatusTask = editedStatusTaskId=>({
    type: types.EDIT_STATUS_TASK,
    editedStatusTaskId
})

export const editTitleTask = editedTask=>({
    type: types.EDIT_TITLE_TASK,
    editedTask
})

export const getOldTask = oldTask=>({
    type: types.GET_OLD_TASK,
    oldTask
})

export const resetFormState = ()=>({
    type: types.RESET_FORM_STATE,
})
