
import * as types from '../configs/action.types'

// const initState = []


// for example
const initState = [
    {
        id: 'abc1-id',
        start: '9/16/2019, 20:20:30 AM',
        title: 'abc1',
        status: false
    },
    {
        id: 'abc2-id',
        start: '9/16/2019, 20:20:30 AM',
        title: 'abc2',
        status: true
    }
]

// state's format
// [{
//     id: 'xxx-xxx',
//     title: 'a string',
//     start: 'a date time',
//     status: true/false
// }]



const generateID = ()=>{
    return `${s3()}-${s3()}`
}

const s3 = ()=>{
    return Math.floor(
        (1-Math.random())*0x10000
    ).toString(16).substring(1)
}

/**
 * Send task list to store
 * @param {*} state
 * @param {*} action
 */
const myReducer = (state=initState, action)=>{
    switch (action.type){

        case types.LIST_ALL:
            return state

        case types.ADD_NEW_TASK:
            const newtask = {
                id: generateID(),
                title: action.newtask,
                status: false,
                start: new Date().toLocaleString()
            }
            // console.log(newtask)
            return [
                ...state,
                newtask
            ]

        case types.DELETE_TASK:
            return state.reduce(
                (acc, curr)=>{
                    if(curr.id !== action.deletedTaskId){
                        acc.push(curr)
                    }
                    return acc
                },
                []
            )

        case types.EDIT_STATUS_TASK:
            const a =  state.reduce(
                (acc, curr)=>{
                    if(curr.id !== action.editedStatusTaskId){
                        acc.push(curr)
                    } else {
                        acc.push({
                            id: curr.id,
                            title: curr.title,
                            status: !curr.status,
                            start: curr.start,
                        })
                    }
                    return acc
                },
                []
            )
            console.log(a)
            return a


        case types.EDIT_TITLE_TASK:
            console.log(action.editedTask)
            const b = state.reduce(
                (acc, curr)=>{
                    if(curr.id !== action.editedTask.id){
                        acc.push(curr)
                    } else {
                        acc.push({
                            id: curr.id,
                            title: action.editedTask.title,
                            status: curr.status,
                            start: curr.start,
                        })
                    }
                    return acc
                },
                []
            )
            console.log(b)
            return b

        default: return state
    }
}


export default myReducer
