import React from 'react';
import { connect } from 'react-redux'

// import * as actions from './../../actions'

import Divider from '@material-ui/core/Divider'

import PresentationTaskList from './tasklist'
import TaskItem from './../taskitem'


const TaskList = ({tasks})=>{

    return (

        <PresentationTaskList>
            {/* {tasks.map(task=>{
                return (
                    <TaskItem
                        key={task.id}
                        task={task}
                    ></TaskItem>
                )
            })} */}
            {tasks.reduce(
                (acc, curr)=>{
                    if(!curr.status){
                        acc.push(
                            <TaskItem
                                key={curr.id}
                                task={curr}
                            ></TaskItem>
                        )
                    }
                    return acc
                },
                []
            )}
            <Divider light />
            {tasks.reduce(
                (acc, curr)=>{
                    if(curr.status){
                        acc.push(
                            <TaskItem
                                key={curr.id}
                                task={curr}
                            ></TaskItem>
                        )
                    }
                    return acc
                },
                []
            )}


        </PresentationTaskList>
    )
}

const mapStateToProps = state=>({
    tasks: state.taskReducer,
})
const mapDispatchToProps = (dispatch, props)=>({

})


export default connect(
    mapStateToProps,
    mapDispatchToProps
)(TaskList)
