import React from 'react';
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import * as actions from '../../actions'

import PresentationTaskForm from './taskform'
// import PresentationTaskForm from './taskform.class'


/**
 *
 * @param {func} onAddTask dispatching addTask action
 * @param {func} handleEditTitleTask dispatching edited title task action
 */
const TaskForm = ({...props})=>{
    return (
        <PresentationTaskForm
            oldtask={props.oldtask}
            handleAddTask = {props.onAddTask}
            handleEditTitleTask = {props.onEditTitleTask}
            handleResetFormState = {props.onResetFormState}
        ></PresentationTaskForm>
    )
}

TaskForm.defaultProps = {
    oldtask: {
        id: '',
        title: ''
    }
  };

TaskForm.propTypes = {
    oldtask: PropTypes.object.isRequired,
    onAddTask: PropTypes.func.isRequired,
    onEditTitleTask: PropTypes.func.isRequired,
    onResetFormState: PropTypes.func.isRequired,

}



const mapStateToProps = state=>({
    oldtask: state.oldtaskReducer,
})

const mapDispatchToProps = (dispatch, props)=>({
    onAddTask: task=>dispatch(actions.addTask(task)),
    onEditTitleTask: task=>dispatch(actions.editTitleTask(task)),
    onResetFormState: ()=>{dispatch(actions.resetFormState())}
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(TaskForm)
