import React from 'react';
import PropTypes from 'prop-types'


import { connect } from 'react-redux'

import * as actions from './../../actions'

import PresentationTaskItem from './taskitem'




/**
 *
 * @param {object} task from parent compoent
 * @param {func} onDeleteTask dispatching deleteTask action
 * @param {func} onEditStatusTask dispatching editStatusTask action
 */
const TaskList = ({...props})=>{

    return (

        <PresentationTaskItem
            task={props.task}
            handleDeleteTask = {props.onDeleteTask}
            handleEditStatusTask = {props.onEditStatusTask}
            handleGetOldTask = {props.onGetOldTask}
            handleResetForm = {props.onResetFormState}
        >
        </PresentationTaskItem>
    )
}

TaskList.propTypes = {
    task: PropTypes.object.isRequired,
    onDeleteTask: PropTypes.func.isRequired,
    onEditStatusTask: PropTypes.func.isRequired,
    onGetOldTask: PropTypes.func.isRequired,
    onResetFormState: PropTypes.func.isRequired,

}


const mapStateToProps = state=>({
})

const mapDispatchToProps = (dispatch, props)=>({
    onDeleteTask: taskid=>dispatch(actions.deleteTask(taskid)),
    onEditStatusTask: taskid=>dispatch(actions.editStatusTask(taskid)),
    onGetOldTask: task=>{dispatch(actions.getOldTask(task))},

    onResetFormState: ()=>{dispatch(actions.resetFormState())}
})


export default connect(
    mapStateToProps,
    mapDispatchToProps
)(TaskList)
